@import "./style.scss";

* {
  box-sizing: border-box;
}

.main-section-component03 {
  width: 100%;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  height: 62rem;
  display: flex;
  justify-content: center;
  .main-section-component03-text-wrapper {
    text-align: center;
    height: 15rem;
    .main-section-component03-text {
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 70%;
      .main-section-component03-title {
        font-size: 2.5rem;
        line-height: 3rem;
        color: #4d4d4d;
        word-break: keep-all;
        font-weight: 800;
        span {
          color: #496895;
        }
      }

      .main-section-component03-nb {
        width: 30rem;
        background-color: #496895;
        border-radius: 4rem;
        color: #fff;
        font-weight: bold;
        font-size: 1.8rem;
        margin-top: 3rem;
        height: 3.5rem;
        line-height: 3.5rem;
      }
    }
  }

  .main-section-component03-circle-wrapper {
    display: flex;
    justify-content: center;
    padding: 3rem 0;

    .main-section-component03-circle01 {
      min-width: 17rem;
      min-height: 17rem;
      background-color: #496895;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      justify-content: space-around;
      margin: 0 1rem;

      .main-section-component03-circle01-inner {
        border: 0.15rem solid $color-white;
        width: 80%;
        height: 80%;
        border-radius: 50%;
        background-color: #496895;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        color: #fff;

        .main-section-component03-circle01-inner-nb,
        .main-section-component03-circle01-inner-nb03 {
          width: 3rem;
          height: 2rem;
          text-align: center;
          font-size: 2rem;
          text-decoration: underline;
          text-underline-position: under;
          margin-bottom: 3rem;
        }
        .main-section-component03-circle01-inner-nb03 {
          padding-top: 1rem;
        }

        h3 {
          font-size: 1.5em;
          text-align: center;
          margin-bottom: 3rem;
          line-height: 2rem;
        }
      }
    }
  }
}

@media all and (max-width: 640px) {
  .main-section-component03 {
    height: 125rem;
    .main-section-component03-text-wrapper {
      margin: 4rem 0;
      margin-top: 6rem;
      .main-section-component03-text {
        .main-section-component03-title {
          line-height: 3.5rem;
        }
        .main-section-component03-nb {
          font-size: 2rem;
          height: 5rem;
          line-height: 5rem;
        }
      }
    }
    .main-section-component03-circle-wrapper {
      flex-direction: column;
      align-items: center;

      .main-section-component03-circle01 {
        width: 24rem;
        height: 24rem;
        background-color: #496895;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        justify-content: space-around;
        margin: 1rem 0;

        .main-section-component03-circle01-inner {
          .main-section-component03-circle01-inner-nb {
            font-size: 2rem;
          }
          h3 {
            font-size: 1.8rem;
          }
        }
      }
    }
  }
}
