@import "../scss/style.scss";

.header-line {
  width: 100%;
}

.header-area {
  position: relative;
  border-bottom: 1.5px solid #bfbfbf;
  background: #ffff;
  text-align: center;
  font-size: 0;
  z-index: 30;
}

.header-back {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  background: #ffff;
  z-index: 20;
  transition: all 400ms;
  height: 0px;
  opacity: 1;
  border-bottom: 1.5px solid #bfbfbf;
}

.header-back-active {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  background: #fff;
  z-index: 20;
  transition: all 400ms;
  height: 370px;
  opacity: 1;
  border-bottom: 1.5px solid #bfbfbf;
}

.header-list {
  display: inline-block;
  overflow: hidden;
  height: 100px;
  transition: all 500ms;
}

.header-list:hover {
  overflow: visible;
}

.header-list:hover ul {
  opacity: 1;
  transition: all 1s;
}

.header-title {
  display: inline-block;
  position: relative;
  padding: 0 20px;
}

.header-title > a {
  display: block;
  position: relative;
  width: 120px;
  height: 100px;
  font-size: 20px;
  line-height: 100px;
  text-decoration: none;
  color: #3e3e3e;
  font-weight: bold;
}

.header-title > a:after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -2px;
  width: 0;
  height: 5px;
  background: $color-contact-yellow;
  transition: all 500ms;
}

.header-title:hover > a:after {
  width: 100%;
  left: 0;
}

.header-title:hover {
  overflow: visible;
}

.header-title > a:hover {
  color: $color-contact-yellow;
}

.header-sub-list {
  position: absolute;
  width: 100%;
  padding: 20px 0 0;
  text-align: center;
  opacity: 0;
  height: 18.75rem;
  margin-left: -20px;
}

.header-list .header-title:nth-of-type(2) .header-sub-list {
  margin-left: -19px;
}

.header-sub-title-down {
  height: 0;
}

.blank {
  cursor: default;
}

.header-sub-title {
  list-style: none;
  line-height: 50px;
}

.header-sub-title > a {
  position: relative;
  text-decoration: none;
  font-size: 18px;
  font-weight: 600;
  color: $color-font-gray;
  transition: all 100ms;

  &:hover {
    color: $color-contact-yellow;
    transition: all 300ms;
  }
}

.header-imgArea {
  position: absolute;
  left: 50px;
  width: 180px;
  height: 100px;
  background: url(../imgs/Header_img/monitLogo_black.png) no-repeat;
  background-size: 100% auto;
  background-position-y: center;
  z-index: 50;
  margin-left: 1.25rem;
}

.header-samsung > p {
  font-size: 0.5rem;
}

.header-samsung-logo {
  display: flex;
  align-items: center;
  width: 100px;
  height: 50px;
  background: url("../imgs/Header_img/samsung_logo.png") no-repeat;
  background-size: 100% auto;
  background-position-y: center;
}

.header-title-samsung {
  position: absolute;
  top: -15px;
  right: 50px;
  color: $color-samsungLogo;
  font-weight: bold;
  list-style: none;
  z-index: 40;
  font-size: 0.8rem;
}

.header-samsung {
  display: flex;
  align-items: center;
  line-height: 130px;
  margin-right: 1.25rem;
}

.hamburger-btn {
  cursor: pointer;
  border: none;
  background: transparent;
}

.bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  -webkit-transiform: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  background-color: #111;
}

.header-area-m {
  display: none;
}

@media screen and (max-width: 1100px) {
  .header-opacity {
    display: none;
    width: 100%;
    height: 100%;
    background: #000;
    position: fixed;
    opacity: 0.5;
    z-index: 50;
  }

  .header-opacity-on {
    display: block;
    width: 100%;
    height: 100%;
    background: #000;
    position: fixed;
    opacity: 0.5;
    z-index: 50;
  }

  .header-area {
    display: none;
  }
  .header-title-samsung {
    display: none;
  }
  .header-back {
    display: none;
  }
  .header-area-m {
    display: flex;
    justify-content: space-between;
    border-bottom: 1.5px solid #bfbfbf;
    background: #ffff;
    text-align: center;
    height: 6.25rem;
    padding: 0 3.125rem;
  }
  .header-imgArea-m {
    display: flex;
    align-items: center;
  }
  .header-img-m {
    width: 11.25rem;
    height: 3.125rem;
    left: 3.125rem;
    background: url(../imgs/Header_img/monitLogo_black.png) no-repeat;
    background-size: 100% auto;
    background-position-y: center;
  }
  .hamburger-area {
    display: flex;
    align-items: center;
    z-index: 60;
  }
}

input[id="hamburger"] {
  display: none;
}

input[id="hamburger"] + label {
  display: block;
  width: 1.875rem;
  height: 1.5625rem;
  position: relative;
  right: 0;
  cursor: pointer;
}

input[id="hamburger"]:checked + label {
  z-index: 2;
  position: fixed;
  margin-right: 3.125rem;
}

input[id="hamburger"] + label span {
  display: block;
  position: absolute;
  width: 100%;
  height: 0.3125rem;
  border-radius: 30px;
  background: #000;
  transition: all 350ms;
}

input[id="hamburger"] + label span:nth-child(1) {
  top: 0;
}

input[id="hamburger"]:checked + label span:nth-child(1) {
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
}

input[id="hamburger"] + label span:nth-child(2) {
  top: 50%;
  transform: translateY(-50%);
}

input[id="hamburger"]:checked + label span:nth-child(2) {
  opacity: 0;
}

input[id="hamburger"] + label span:nth-child(3) {
  bottom: 0;
}

input[id="hamburger"]:checked + label span:nth-child(3) {
  bottom: 50%;
  transform: translateY(50%) rotate(-45deg);
}

input[id="hamburger"]:checked + label + .hamburger-sidebar {
  right: 0px;
}

.hamburger-sidebar {
  width: 25rem;
  height: 100%;
  background: #fff;
  position: fixed;
  top: 0;
  right: -25rem;
  z-index: 1;
  transition: all 400ms;
  border-left: 1.5px solid #bfbfbf;
  padding-top: 6.125rem;
}

input[id*="accordion"] {
  display: none;
}
input[id*="accordion"] + label {
  display: block;
  position: relative;
  padding: 20px;
  border: 1px solid #fff;
  color: #fff;
  background: $color-main-blue;
  cursor: pointer;
  font-weight: bold;
  font-size: 1.625rem;
}
input[id*="accordion"] + label p {
  position: absolute;
  top: 50%;
  right: 10px;
  width: 30px;
  height: 30px;
  margin-top: -12px;
  font-size: 25px;
  transition: all 400ms;
}

input[id*="accordion"]:checked + label p {
  display: none;
  transition: all 400ms;
}

input[id*="accordion"] + label em {
  display: none;
  position: absolute;
  top: 50%;
  right: 10px;
  width: 30px;
  height: 30px;
  margin-top: -12px;
  font-size: 25px;
  transition: all 400ms;
}

input[id*="accordion"]:checked + label em {
  display: block;
  transition: all 400ms;
}

input[id*="accordion"] + label + .accordion-menu-list {
  max-height: 0;
  transition: all 400ms;
  overflow: hidden;
  background: #d8dad3;
  font-size: 0.75rem;
}

input[id*="accordion"] + label + .accordion-menu-list a {
  display: inline-block;
  padding: 20px;
}

input[id*="accordion"]:checked + label + .accordion-menu-list {
  max-height: 500px;
}

.accordion {
  height: 100%;
  overflow-y: auto;
}

.accordion::-webkit-scrollbar {
  display: none;
}

.accordion-menu-list {
  display: flex;
  flex-direction: column;
}

.accordion-menu-list a {
  text-decoration: none;
  color: #111;
  font-weight: bold;
  font-size: 1.625rem;
  border: 1px solid #fff;
  &:hover {
    text-decoration: underline;
  }
}
